import MemoAbstract from "jx/core/memos/MemoAbstract";
/*
*/
class MemoCookie extends MemoAbstract {
    constructor(dataObject) {
        super(Object.assign({}, dataObject));
    }
    save() {
        this._createCookie("memo", JSON.stringify(this.data), 1000000);
    }
    load() {
        this.data = JSON.parse(this._readCookie("memo")) || {};
    }
    clear() {
        this.data = {};
        this.save();
    }
    _createCookie(name, value, days) {
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
            var expires = "; expires=" + date.toGMTString();
        }
        else
            var expires = "";
        document.cookie = name + "=" + value + expires + "; path=/";
    }
    _readCookie(name) {
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ')
                c = c.substring(1, c.length);
            if (c.indexOf(nameEQ) == 0)
                return c.substring(nameEQ.length, c.length);
        }
        return null;
    }
    _eraseCookie(name) {
        this._createCookie(name, "", -1);
    }
}
export default MemoCookie;
